<template>
  <div class="main">
    <div class="message">
      <h2>{{ message }}</h2>
      <h4 v-if="$store.state.lang !== 'en'">
        You don't currently support IE. Please select Chrome, Firefox, Safari browser to download and use.
        Please forgive you for your inconvenience
      </h4>
    </div>
    <ul class="browser">
      <li>
        <a href="https://www.google.com/chrome/" target="blank">
          <img :src="`${baseUrl}img/error/chorme.png`" alt />
        </a>
      </li>
      <li>
        <a href="http://mozilla.com.tw/firefox/new/" target="blank">
          <img :src="`${baseUrl}img/error/firefox.png`" alt />
        </a>
      </li>
      <li>
        <a href="https://support.apple.com/downloads#safari" target="blank">
          <img :src="`${baseUrl}img/error/safari.png`" alt />
        </a>
      </li>
    </ul>
    <div class="ie">
      <img :src="`${baseUrl}img/error/ie.png`" alt />
    </div>
  </div>
</template>

<script>
export default {
  name: "",
  props: {},
  data: function() {
    // 資料
    return {
      baseUrl: process.env.VUE_APP_ROUTER_BASE,
      message: '' // *initLanguage*
      // message: '您目前不支援 IE ，請點選擇 Chrome、Firefox、Safari 瀏覽器下載使用。造成您的不便請您見諒。'
    };
  },
  watch: {
    '$store.state.lang' (value) {
      this.initLanguage()
    }
  },
  computed: {
    //相依的資料改變時才做計算方法
  },
  methods: {
    // 初始語系
    initLanguage () {
      this.message = this.$ln('您目前不支援 IE ，請點選擇 Chrome、Firefox、Safari 瀏覽器下載使用。造成您的不便請您見諒。')
    }
  },
  //BEGIN--生命週期
  beforeCreate: function() {
    //實體初始化
  },
  created: function() {
    //實體建立完成。資料 data 已可取得，但 el 屬性還未被建立。
  },
  beforeMount: function() {
    //執行元素掛載之前。
  },
  mounted: function() {
    this.initLanguage()
  },
  beforeUpdate: function() {
    //當資料變化時被呼叫，還不會描繪 View。
  },
  updated: function() {
    //當資料變化時被呼叫，還不會描繪 View。
  },
  beforeDestroy: function() {
    //實體還可使用。
  },
  destroyed: function() {
    //實體銷毀。
  }
  //END--生命週期
};
</script>

<style lang="scss">
body {
  margin: 0;
  padding: 0;
  background: #e2e2e2;
  color: #636363;
  font-family: Arial, Helvetica, sans-serif;
}

.main {
  width: 100%;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translateX(-50%) translateY(-50%);
}

.message {
  width: 68%;
  margin: 0 auto;
}

.browser {
  list-style-type: none;
  width: 228px;
  margin: 0 auto;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 8px;
}

.ie {
  width: 510px;
  margin: 0 auto;
  margin-top: 30px;
}

@media (max-width: 1024px) {
  .message {
    width: 86%;
  }
}

@media (max-width: 576px) {
  .message {
    font-size: 14px;
  }

  .browser img {
    width: 75%;
  }

  .ie {
    width: 350px;
    margin-top: 20px;
  }

  .ie img {
    width: 100%;
  }
}

.ie-error {
  height: 500px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 20px;
  & div {
    padding: 10px;
    color: red;
  }
  & .img {
    background-size: contain;
    width: 400px;
    height: inherit;
  }
}
</style>
